.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 60px;
  background-color: var(--footer-background-colour);
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  max-height: 10px;
  max-width: 1200px;
  margin: 0 auto;
}

.text-muted {
  font-size: 14px;
  color: #999999;
}

.icon-container {
  display: flex;
  justify-content: center;
  align-items: center; 

}

.icon {
  font-size: 30px;
  margin-right: 10px;
  margin-left: 10px;
  
}